.wrapper {
  width: 100%;
  display: flex;
  justify-content: space-between;
  padding: 10px;
  align-items: center;

  .logo {
    display: flex;
    justify-content: center;

    .logo_img {
      margin: auto;
    }
  }

  .navbar {
    display: flex;

    li {
      margin: 7px;
    }
    .totalPrice{
      font-size: 1.2rem;
      opacity: 0.7;
      cursor: pointer;

      &:hover{
        opacity: 0.9;
      }
    }
    img {
      cursor: pointer;
      opacity: 0.8;
      transition: 0.15s;

      &:hover {
        opacity: 1;
      }
    }
  }
}
@media screen and (min-width: 320px) and (max-width: 480px) {

  .wrapper {
    width: 100vw;
    padding: 5px;
    display: inline-flex;
    justify-content: normal;

   .logo{
    width: 220px;
     padding: 0;
     margin: 0;

     .logo_img {
       height: 60px;
     }
     h3 {
       font-size: 16px;
       text-align: center;
     }
     p {
       font-size: 12px;
       text-align: center;
       line-height: 10px;
     }
   }

    .navbar {
      width: 100%;
      height: 100%;
      display: flex;
      justify-content: space-around;;
      flex-wrap: nowrap;
      align-items: center;

      img {
        width: 22px;
        height: 22px;
        margin: 0;
        padding: 0;
      }

      .totalPrice {
        position: absolute;
        margin-top: 3px;
        white-space: nowrap;
        font-size: 14px;
        min-width: 20px;
        margin-left: 0;
      }

     .basket_item, .favorite_item, .order_item {
     img {
       display: flex;
       flex: 1;
       padding: 0;
       margin: 0;
       flex-wrap: nowrap;
       align-items: center;
     }
     }
    }
  }
}

@media screen and (min-width: 481px) and (max-width: 768px) {

  .wrapper {
    display: flex;
    justify-content: space-between;

    .logo {
      width: 220px;
      padding: 0;
      margin: 0;

      .logo_img {
        height: 70px;
      }

      h3 {
        font-size: 1.4rem;
        text-align: center;
      }

      p {
        font-size: 1rem;
        text-align: center;
        line-height: 14px;
      }
    }

    .navbar {
      display: flex;
      flex-wrap: nowrap;

      li{
        margin-left: 10px;
        img {
          width: 26px;
          height: 26px;
          margin: 0;
          padding: 0;
        }
      }
      .basket_item {
        display: flex;
        align-items: center;

        .totalPrice {
          white-space: nowrap;
          font-size: 1rem;
          align-items: center;
          margin-left: 10px;
        }
      }



    }

  }
}



