.search {
  display: flex;
  justify-content: space-between;
  margin-bottom: 40px;
  align-items: center;
}

h2 {
  margin-left: 0;
}

label {
  display: flex;
  height: 32px;

  border: none;

  input {
    font-size: 16px;
    border: none;
  }

  .imgSearch {
    width: 16px;
    margin-right: 8px;
    opacity: 0.5;
    cursor: pointer;
    transition: 0.13s;
  }
  .imgSearch:hover {
    opacity: 1;
  }

  .buttonClose {
    width: 25px;
    opacity: 0.5;
    cursor: pointer;
    transition: 0.15s;
  }
  .buttonClose:hover {
    opacity: 1;
  }
}

@media screen and (min-width: 320px) and (max-width: 480px) {
  .search {
  flex-wrap: wrap;
  margin-bottom: 0;

    h2 {
      text-align: center;
      font-size: 20px;
      width: 100%;
      border-radius: 10px;

    }
    label {
      left: 0;
      width: 100%;
      justify-content: center;
      border: 1px solid #f8f8f8;
      border-radius: 10px;
      box-shadow: 0 3px 7px rgba(0, 0, 0, 0.07);


      input {
        font-size: 12px;

      }



    }
  }


}

